	module.exports = {"data":{"postsJson":{"title":"Cornelius' Conversion","questionsTitle":"COMMANDS & PROMISES","display":"block","bookName":"사도행전","ref":"9:32-12:25","html":"<p>   <strong>9</strong><u>32</u>그 때에 베드로가 사방으로 두루 다니다가 룻다에 사는 성도들에게도 내려갔더니 <u>33</u>거기서 애니아라 하는 사람을 만나매 그는 중풍병으로 침상 위에 누운 지 여덟 해라 <u>34</u>베드로가 이르되</p><h1>시몬 베드로</h1><h3>애니아야 예수 그리스도께서 너를 낫게 하시니 일어나 네 자리를 정돈하라</h3><p>한대 곧 일어나니 <u>35</u>룻다와 사론에 사는 사람들이 다 그를 보고 주께로 돌아오니라<br />   <u>36</u>욥바에 다비다라 하는 여제자가 있으니 그 이름을 번역하면 도르가라 선행과 구제하는 일이 심히 많더니 <u>37</u>그 때에 병들어 죽으매 시체를 씻어 다락에 누이니라 <u>38</u>룻다가 욥바에서 가까운지라 제자들이 베드로가 거기 있음을 듣고 두 사람을 보내어 지체 말고 와 달라고 간청하여 <u>39</u>베드로가 일어나 그들과 함께 가서 이르매 그들이 데리고 다락방에 올라가니 모든 과부가 베드로 곁에 서서 울며 도르가가 그들과 함께 있을 때에 지은 속옷과 겉옷을 다 내보이거늘 <u>40</u>베드로가 사람을 다 내보내고 무릎을 꿇고 기도하고 돌이켜 시체를 향하여 이르되</p><h1>시몬 베드로</h1><h3>다비다야 일어나라</h3><p>하니 그가 눈을 떠 베드로를 보고 일어나 앉는지라 <u>41</u>베드로가 손을 내밀어 일으키고 성도들과 과부들을 불러 들여 그가 살아난 것을 보이니 <u>42</u>온 욥바 사람이 알고 많은 사람이 주를 믿더라 <u>43</u>베드로가 욥바에 여러 날 있어 시몬이라 하는 무두장이의 집에서 머무니라<br />   <strong>10</strong><u>1</u>가이사랴에 고넬료라 하는 사람이 있으니 이달리야 부대라 하는 군대의 백부장이라 <u>2</u>그가 경건하여 온 집안과 더불어 하나님을 경외하며 백성을 많이 구제하고 하나님께 항상 기도하더니 <u>3</u>하루는 제 구 시쯤 되어 환상 중에 밝히 보매 하나님의 사자가 들어와 이르되</p><h1>천사</h1><h4>고넬료야</h4><p>하니 <u>4</u>고넬료가 주목하여 보고 두려워 이르되</p><h1>고넬료</h1><h4>주여 무슨 일이니이까</h4><p>천사가 이르되</p><h1>천사</h1><h4>네 기도와 구제가 하나님 앞에 상달되어 기억하신 바가 되었으니 <u>5</u>네가 지금 사람들을 욥바에 보내어 베드로라 하는 시몬을 청하라 <u>6</u>그는 무두장이 시몬의 집에 유숙하니 그 집은 해변에 있다</h4><p>하더라<br />   <u>7</u>마침 말하던 천사가 떠나매 고넬료가 집안 하인 둘과 부하 가운데 경건한 사람 하나를 불러 <u>8</u>이 일을 다 이르고 욥바로 보내니라<br />   <u>9</u>이튿날 그들이 길을 가다가 그 성에 가까이 갔을 그 때에 베드로가 기도하려고 지붕에 올라가니 그 시각은 제 육 시더라 <u>10</u>그가 시장하여 먹고자 하매 사람들이 준비할 때에 황홀한 중에 <u>11</u>하늘이 열리며 한 그릇이 내려오는 것을 보니 큰 보자기 같고 네 귀를 매어 땅에 드리웠더라 <u>12</u>그 안에는 땅에 있는 각종 네 발 가진 짐승과 기는 것과 공중에 나는 것들이 있더라<br />   <u>13</u>또 소리가 있으되</p><h1>하나님</h1><h2><em>베드로야 일어나 잡아 먹어라</em></h2><p>하거늘 <u>14</u>베드로가 이르되</p><h1>시몬 베드로</h1><h3>주여 그럴 수 없나이다 속되고 깨끗하지 아니한 것을 내가 결코 먹지 아니하였나이다</h3><p>한대 <u>15</u>또 두 번째 소리가 있으되</p><h1>하나님</h1><h2><em>하나님께서 깨끗하게 하신 것을 네가 속되다 하지 말라</em></h2><p>하더라 <u>16</u>이런 일이 세 번 있은 후 그 그릇이 곧 하늘로 올려져 가니라<br />   <u>17</u>베드로가 본 바 환상이 무슨 뜻인지 속으로 의아해 하더니 마침 고넬료가 보낸 사람들이 시몬의 집을 찾아 문 밖에 서서 <u>18</u>불러 묻되</p><h1>고넬료의 사자들</h1><h4>베드로라 하는 시몬이 여기 유숙하느냐</h4><p>하거늘 <u>19</u>베드로가 그 환상에 대하여 생각할 때에 성령께서 그에게 말씀하시되</p><h1>하나님</h1><h2><em>두 사람이 너를 찾으니 </em><u><em>20</em></u><em>일어나 내려가 의심하지 말고 함께 가라 내가 그들을 보내었느니라</em></h2><p>하시니 <u>21</u>베드로가 내려가 그 사람들을 보고 이르되</p><h1>시몬 베드로</h1><h3>내가 곧 너희가 찾는 사람인데 너희가 무슨 일로 왔느냐</h3><p><u>22</u>그들이 대답하되</p><h1>고넬료의 사자들</h1><h4>백부장 고넬료는 의인이요 하나님을 경외하는 사람이라 유대 온 족속이 칭찬하더니 그가 거룩한 천사의 지시를 받아 당신을 그 집으로 청하여 말을 들으려 하느니라</h4><p>한대 <u>23</u>베드로가 불러 들여 유숙하게 하니라 이튿날 일어나 그들과 함께 갈새 욥바에서 온 어떤 형제들도 함께 가니라<br />   <u>24</u>이튿날 가이사랴에 들어가니 고넬료가 그의 친척과 가까운 친구들을 모아 기다리더니 <u>25</u>마침 베드로가 들어올 때에 고넬료가 맞아 발 앞에 엎드리어 절하니 <u>26</u>베드로가 일으켜 이르되</p><h1>시몬 베드로</h1><h3>일어서라 나도 사람이라</h3><p>하고 <u>27</u>더불어 말하며 들어가 여러 사람이 모인 것을 보고<u>28</u>이르되</p><h1>시몬 베드로</h1><h3>유대인으로서 이방인과 교제하며 가까이 하는 것이 위법인 줄은 너희도 알거니와 하나님께서 내게 지시하사 아무도 속되다 하거나 깨끗하지 않다 하지 말라 하시기로 <u>29</u>부름을 사양하지 아니하고 왔노라 묻노니 무슨 일로 나를 불렀느냐</h3><p><u>30</u>고넬료가 이르되</p><h1>고넬료</h1><h4>내가 나흘 전 이맘때까지 내 집에서 제 구 시 기도를 하는데 갑자기 한 사람이 빛난 옷을 입고 내 앞에 서서 <u>31</u>말하되 고넬료야 하나님이 네 기도를 들으시고 네 구제를 기억하셨으니 <u>32</u>사람을 욥바에 보내어 베드로라 하는 시몬을 청하라 그가 바닷가 무두장이 시몬의 집에 유숙하느니라 하시기로 <u>33</u>내가 곧 당신에게 사람을 보내었는데 오셨으니 잘하였나이다 이제 우리는 주께서 당신에게 명하신 모든 것을 듣고자 하여 다 하나님 앞에 있나이다</h4><p><u>34</u>베드로가 입을 열어 말하되</p><h1>시몬 베드로</h1><h3>내가 참으로 하나님은 사람의 외모를 보지 아니하시고 <u>35</u>각 나라 중 하나님을 경외하며 의를 행하는 사람은 다 받으시는 줄 깨달았도다 <u>36</u>만유의 주 되신 예수 그리스도로 말미암아 화평의 복음을 전하사 이스라엘 자손들에게 보내신 말씀 <u>37</u>곧 요한이 그 세례를 반포한 후에 갈릴리에서 시작하여 온 유대에 두루 전파된 그것을 너희도 알거니와 <u>38</u>하나님이 나사렛 예수에게 성령과 능력을 기름 붓듯 하셨으매 그가 두루 다니시며 선한 일을 행하시고 마귀에게 눌린 모든 사람을 고치셨으니 이는 하나님이 함께 하셨음이라<br />   <u>39</u>우리는 유대인의 땅과 예루살렘에서 그가 행하신 모든 일에 증인이라 그를 그들이 나무에 달아 죽였으나 <u>40</u>하나님이 사흘 만에 다시 살리사 나타내시되 <u>41</u>모든 백성에게 하신 것이 아니요 오직 미리 택하신 증인 곧 죽은 자 가운데서 부활하신 후 그를 모시고 음식을 먹은 우리에게 하신 것이라 <u>42</u>우리에게 명하사 백성에게 전도하되 하나님이 살아 있는 자와 죽은 자의 재판장으로 정하신 자가 곧 이 사람인 것을 증언하게 하셨고 <u>43</u>그에 대하여 모든 선지자도 증언하되 그를 믿는 사람들이 다 그의 이름을 힘입어 죄 사함을 받는다 하였느니라</h3><p><u>44</u>베드로가 이 말을 할 때에 성령이 말씀 듣는 모든 사람에게 내려오시니 <u>45</u>베드로와 함께 온 할례 받은 신자들이 이방인들에게도 성령 부어 주심으로 말미암아 놀라니 <u>46</u>이는 방언을 말하며 하나님 높임을 들음이러라<br />   <u>47</u>이에 베드로가 이르되</p><h1>시몬 베드로</h1><h3>이 사람들이 우리와 같이 성령을 받았으니 누가 능히 물로 세례 베풂을 금하리요</h3><p>하고 <u>48</u>명하여</p><h1>시몬 베드로</h1><h3>예수 그리스도의 이름으로 세례를 베풀라</h3><p>하니라 그들이 베드로에게 며칠 더 머물기를 청하니라<br />   <strong>11</strong><u>1</u>유대에 있는 사도들과 형제들이 이방인들도 하나님의 말씀을 받았다 함을 들었더니 <u>2</u>베드로가 예루살렘에 올라갔을 때에 할례자들이 비난하여 <u>3</u>이르되</p><h1>예루살렘의 믿는 사람들</h1><h4>네가 무할례자의 집에 들어가 함께 먹었다</h4><p>하니<u>4</u>베드로가 그들에게 이 일을 차례로 설명하여 <u>5</u>이르되</p><h1>시몬 베드로</h1><h3>내가 욥바 시에서 기도할 때에 황홀한 중에 환상을 보니 큰 보자기 같은 그릇이 네 귀에 매어 하늘로부터 내리어 내 앞에까지 드리워지거늘<u>6</u>이것을 주목하여 보니 땅에 네 발 가진 것과 들짐승과 기는 것과 공중에 나는 것들이 보이더라 <u>7</u>또 들으니 소리 있어 내게 이르되 베드로야 일어나 잡아 먹으라 하거늘<br />   <u>8</u>내가 이르되 주님 그럴 수 없나이다 속되거나 깨끗하지 아니한 것은 결코 내 입에 들어간 일이 없나이다 하니<br />   <u>9</u>또 하늘로부터 두 번째 소리 있어 내게 이르되 하나님이 깨끗하게 하신 것을 네가 속되다고 하지 말라 하더라 <u>10</u>이런 일이 세 번 있은 후에 모든 것이 다시 하늘로 끌려 올라가더라<br />   <u>11</u>마침 세 사람이 내가 유숙한 집 앞에 서 있으니 가이사랴에서 내게로 보낸 사람이라 <u>12</u>성령이 내게 명하사 아무 의심 말고 함께 가라 하시매 이 여섯 형제도 나와 함께 가서 그 사람의 집에 들어가니 <u>13</u>그가 우리에게 말하기를 천사가 내 집에 서서 말하되 네가 사람을 욥바에 보내어 베드로라 하는 시몬을 청하라 <u>14</u>그가 너와 네 온 집이 구원 받을 말씀을 네게 이르리라 함을 보았다 하거늘 <u>15</u>내가 말을 시작할 때에 성령이 그들에게 임하시기를 처음 우리에게 하신 것과 같이 하는지라 <u>16</u>내가 주의 말씀에 요한은 물로 세례를 베풀었으나 너희는 성령으로 세례를 받으리라 하신 것이 생각났노라 <u>17</u>그런즉 하나님이 우리가 주 예수 그리스도를 믿을 때에 주신 것과 같은 선물을 그들에게도 주셨으니 내가 누구이기에 하나님을 능히 막겠느냐</h3><p>하더라<br />   <u>18</u>그들이 이 말을 듣고 잠잠하여 하나님께 영광을 돌려 이르되</p><h1>예루살렘의 믿는 사람들</h1><h4>그러면 하나님께서 이방인에게도 생명 얻는 회개를 주셨도다</h4><p>하니라<br />   <u>19</u>그 때에 스데반의 일로 일어난 환난으로 말미암아 흩어진 자들이 베니게와 구브로와 안디옥까지 이르러 유대인에게만 말씀을 전하는데 <u>20</u>그 중에 구브로와 구레네 몇 사람이 안디옥에 이르러 헬라인에게도 말하여 주 예수를 전파하니 <u>21</u>주의 손이 그들과 함께 하시매 수많은 사람들이 믿고 주께 돌아오더라<br />   <u>22</u>예루살렘 교회가 이 사람들의 소문을 듣고 바나바를 안디옥까지 보내니 <u>23</u>그가 이르러 하나님의 은혜를 보고 기뻐하여 모든 사람에게 굳건한 마음으로 주와 함께 머물러 있으라권하니 <u>24</u>바나바는 착한 사람이요 성령과 믿음이 충만한 사람이라 이에 큰 무리가 주께 더하여지더라</h4><p><u>25</u>바나바가 사울을 찾으러 다소에 가서 <u>26</u>만나매 안디옥에 데리고 와서 둘이 교회에 일 년간 모여 있어 큰 무리를 가르쳤고 제자들이 안디옥에서 비로소 그리스도인이라 일컬음을 받게 되었더라<br />   <u>27</u>그 때에 선지자들이 예루살렘에서 안디옥에 이르니 <u>28</u>그 중에 아가보라 하는 한 사람이 일어나 성령으로 말하되</p><h1>아가보</h1><h4>천하에 큰 흉년이 들리라</h4><p>하더니 글라우디오 때에 그렇게 되니라 <u>29</u>제자들이 각각 그 힘대로 유대에 사는 형제들에게 부조를 보내기로 작정하고 <u>30</u>이를 실행하여 바나바와 사울의 손으로 장로들에게 보내니라<br />   <strong>12</strong><u>1</u>그 때에 헤롯 왕이 손을 들어 교회 중에서 몇 사람을 해하려 하여 <u>2</u>요한의 형제 야고보를 칼로 죽이니 <u>3</u>유대인들이 이 일을 기뻐하는 것을 보고 베드로도 잡으려 할새 때는 무교절 기간이라 <u>4</u>잡으매 옥에 가두어 군인 넷씩인 네 패에게 맡겨 지키고 유월절 후에 백성 앞에 끌어 내고자 하더라 <u>5</u>이에 베드로는 옥에 갇혔고 교회는 그를 위하여 간절히 하나님께 기도하더라<br />   <u>6</u>헤롯이 잡아 내려고 하는 그 전날 밤에 베드로가 두 군인 틈에서 두 쇠사슬에 매여 누워 자는데 파수꾼들이 문 밖에서 옥을 지키더니 <u>7</u>홀연히 주의 사자가 나타나매 옥중에 광채가 빛나며 또 베드로의 옆구리를 쳐 깨워 이르되</p><h1>천사</h1><h4>급히 일어나라</h4><p>하니 쇠사슬이 그 손에서 벗어지더라 <u>8</u>천사가 이르되</p><h1>천사</h1><h4>띠를 띠고 신을 신으라</h4><p>하거늘 베드로가 그대로 하니 천사가 또 이르되</p><h1>천사</h1><h4>겉옷을 입고 따라오라</h4><p>한대<u>9</u>베드로가 나와서 따라갈새 천사가 하는 것이 생시인 줄 알지 못하고 환상을 보는가 하니라 <u>10</u>이에 첫째와 둘째 파수를 지나 시내로 통한 쇠문에 이르니 문이 저절로 열리는지라 나와서 한 거리를 지나매 천사가 곧 떠나더라</h1><p><u>11</u>이에 베드로가 정신이 들어 이르되</p><h1>시몬 베드로</h1><h3>내가 이제야 참으로 주께서 그의 천사를 보내어 나를 헤롯의 손과 유대 백성의 모든 기대에서 벗어나게 하신 줄 알겠노라</h3><p>하여 <u>12</u>깨닫고 마가라 하는 요한의 어머니 마리아의 집에 가니 여러 사람이 거기에 모여 기도하고 있더라 <u>13</u>베드로가 대문을 두드린대 로데라 하는 여자 아이가 영접하러 나왔다가 <u>14</u>베드로의 음성인 줄 알고 기뻐하여 문을 미처 열지 못하고 달려 들어가 말하되</p><h1>로데</h1><h4>베드로가 대문 밖에 섰더라</h4><p>하니 <u>15</u>그들이 말하되</p><h1>예루살렘의 믿는 사람들</h1><h4>네가 미쳤다</h4><p>하나 여자 아이는 힘써 말하되</p><h1>로데</h1><h4>참말이라</h4><p>하니 그들이 말하되</p><h1>예루살렘의 믿는 사람들</h1><h4>그러면 그의 천사라</h4><p>하더라<br />   <u>16</u>베드로가 문 두드리기를 그치지 아니하니 그들이 문을 열어 베드로를 보고 놀라는지라 <u>17</u>베드로가 그들에게 손짓하여 조용하게 하고 주께서 자기를 이끌어 옥에서 나오게 하던 일을 말하고 또 야고보와 형제들에게 이 말을 전하라 하고 떠나 다른 곳으로 가니라<br />   <u>18</u>날이 새매 군인들은 베드로가 어떻게 되었는지 알지 못하여 적지 않게 소동하니 <u>19</u>헤롯이 그를 찾아도 보지 못하매 파수꾼들을 심문하고 죽이라 명하니라 헤롯이 유대를 떠나 가이사랴로 내려가서 머무니라<br />   <u>20</u>헤롯이 두로와 시돈 사람들을 대단히 노여워하니 그들의 지방이 왕국에서 나는 양식을 먹는 까닭에 한마음으로 그에게 나아와 왕의 침소 맡은 신하 블라스도를 설득하여 화목하기를 청한지라 <u>21</u>헤롯이 날을 택하여 왕복을 입고 단상에 앉아 백성에게 연설하니 <u>22</u>백성들이 크게 부르되</p><h1>두로와 시돈 사람들</h1><h4>이것은 신의 소리요 사람의 소리가 아니라</h4><p>하거늘 <u>23</u>헤롯이 영광을 하나님께로 돌리지 아니하므로 주의 사자가 곧 치니 벌레에게 먹혀 죽으니라<br /><u>24</u>하나님의 말씀은 흥왕하여 더하더라 <u>25</u>바나바와 사울이 부조하는 일을 마치고 마가라 하는 요한을 데리고 예루살렘에서 돌아오니라</p>","path":"/356","Question1":"What God-given guidance can you observe in this passage?","Question2":"Put yourself in the passage and envision what it might have been like to receive this guidance. How does this relate to you and your family today?","Question3":"What God-inspired encouragement can you observe in this passage? How can you experience more of this in your own life?","Question4":"Write a poem about what this encouragement means for you.","prevPath":"/355","nextPath":"/357"}},"pathContext":{}}

